.catalog {
  padding-top: 80px;
  min-height: 100vh;
}

/* Catalog Hero */
.catalog-hero {
  background: linear-gradient(135deg, #f8fafc 0%, #ffffff 100%);
  padding: 4rem 0;
  color: #1e293b;
  border-bottom: 1px solid #e2e8f0;
}

.catalog-header {
  text-align: center;
}

.catalog-header h1 {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

.catalog-header p {
  font-size: 1.2rem;
  color: #64748b;
  max-width: 600px;
  margin: 0 auto;
}

/* Catalog Content */
.catalog-content {
  padding: 4rem 0;
  background: #ffffff;
}

/* Controls */
.catalog-controls {
  display: flex;
  gap: 2rem;
  margin-bottom: 3rem;
  flex-wrap: wrap;
}

.search-box, .filter-box {
  display: flex;
  align-items: center;
  background: #ffffff;
  border: 2px solid #e2e8f0;
  border-radius: 16px;
  padding: 1rem 1.25rem;
  gap: 0.75rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.search-box::before, .filter-box::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(59, 130, 246, 0.1), transparent);
  transition: left 0.5s;
}

.search-box:focus-within::before, .filter-box:focus-within::before {
  left: 100%;
}

.search-box:focus-within, .filter-box:focus-within {
  border-color: #3b82f6;
  box-shadow: 0 0 0 4px rgba(59, 130, 246, 0.1), 0 8px 30px rgba(0, 0, 0, 0.12);
  transform: translateY(-2px);
}

.search-box:hover, .filter-box:hover {
  border-color: #cbd5e1;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.12);
  transform: translateY(-2px);
}

.search-box {
  flex: 1;
  min-width: 320px;
}

.filter-box {
  min-width: 220px;
}

.search-box svg, .filter-box svg {
  color: #3b82f6;
  flex-shrink: 0;
}

.search-box input {
  border: none;
  outline: none;
  flex: 1;
  font-size: 1rem;
  color: #1e293b;
  background: transparent;
  font-weight: 500;
}

.search-box input::placeholder {
  color: #94a3b8;
  font-weight: 400;
}

.filter-box select {
  border: none;
  outline: none;
  background: none;
  font-size: 1rem;
  color: #1e293b;
  cursor: pointer;
  flex: 1;
  font-weight: 500;
}

/* Products Grid */
.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  gap: 2rem;
}

.product-card {
  background: #ffffff;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.06);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid #f1f5f9;
  position: relative;
}

.product-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #3b82f6, #8b5cf6, #06b6d4);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.product-card:hover::before {
  opacity: 1;
}

.product-card:hover {
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.12);
  transform: translateY(-8px);
}

.product-image {
  position: relative;
  background: linear-gradient(135deg, #f8fafc 0%, #f1f5f9 100%);
  padding: 3rem 2rem;
  text-align: center;
  border-bottom: 1px solid #f1f5f9;
}

.product-svg {
  width: 80px;
  height: 80px;
  margin-bottom: 1rem;
  filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.1));
}

.product-svg svg {
  width: 100%;
  height: 100%;
}

.product-emoji {
  font-size: 4rem;
  display: block;
  margin-bottom: 1rem;
  filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.1));
}

.product-category {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: linear-gradient(135deg, #3b82f6, #8b5cf6);
  color: #ffffff;
  padding: 0.5rem 1rem;
  border-radius: 25px;
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  box-shadow: 0 2px 10px rgba(59, 130, 246, 0.3);
}

.product-info {
  padding: 2.5rem;
}

.product-info h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1e293b;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.product-description {
  color: #64748b;
  line-height: 1.6;
  margin-bottom: 2rem;
  font-size: 0.95rem;
}

.product-specs {
  margin-bottom: 2rem;
}

.product-specs h4 {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 1.1rem;
  font-weight: 600;
  color: #1e293b;
  margin-bottom: 1rem;
}

.product-specs svg {
  color: #3b82f6;
}

.specs-list {
  display: grid;
  gap: 0.75rem;
}

.spec-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background: #f8fafc;
  border-radius: 12px;
  border-left: 4px solid #3b82f6;
  transition: all 0.3s ease;
}

.spec-item:hover {
  background: #f1f5f9;
  transform: translateX(4px);
}

.spec-key {
  font-weight: 500;
  color: #475569;
}

.spec-value {
  font-weight: 600;
  color: #1e293b;
}

.price-request-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  width: 100%;
  padding: 1rem;
  background: transparent;
  color: #3b82f6;
  border: 2px solid #3b82f6;
  border-radius: 8px;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.price-request-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(59, 130, 246, 0.1), transparent);
  transition: left 0.6s ease;
}

.price-request-btn:hover::before {
  left: 100%;
}

.price-request-btn:hover {
  background: #3b82f6;
  color: #ffffff;
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(59, 130, 246, 0.25);
}

/* No Results */
.no-results {
  text-align: center;
  padding: 4rem 2rem;
  color: #64748b;
}

.no-results svg {
  margin-bottom: 1rem;
  color: #94a3b8;
}

.no-results h3 {
  font-size: 1.5rem;
  font-weight: 600;
  color: #475569;
  margin-bottom: 0.5rem;
}

.no-results p {
  font-size: 1rem;
  line-height: 1.6;
}

/* Responsive Design */
@media (max-width: 768px) {
  .catalog-header h1 {
    font-size: 2.5rem;
  }
  
  .catalog-controls {
    flex-direction: column;
    gap: 1rem;
  }
  
  .search-box, .filter-box {
    min-width: auto;
  }
  
  .products-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .product-card {
    margin: 0 1rem;
  }
}

@media (max-width: 480px) {
  .catalog-hero {
    padding: 2rem 0;
  }
  
  .catalog-header h1 {
    font-size: 2rem;
  }
  
  .catalog-header p {
    font-size: 1rem;
  }
  
  .product-info {
    padding: 1.5rem;
  }
  
  .spec-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
}